@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

:root {
  --background-color-light: #efe4d7;
  --background-color-dark: #5b5b5b;
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--background-color-light);
  margin: 0;
  font-family: 'Poppins';
}

nav {
  background: var(--background-color-dark);
  position: fixed;
  width: 100vw;
  z-index: 1;

  .navbar-text {
    font-weight: bold;
    color: var(--background-color-light);
    font-size: 1.2rem;
    padding: 10px;
  }
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    float: right;
  }

  li {
    float: right;
    padding-right: 1.4vw;
  }

  li a {
    display: inline-block;
    color: var(--background-color-light);
    text-align: center;
    padding: 10px;
    text-decoration: none;
    font-size: 1.2rem;
    padding-right: 1vw;
    padding-left: 2vw;
  }
}

.main-section {
  background-color: var(--background-color-light);
  background-image: url('./local_files/Frame-1.png');
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
  .main-text {
    font-size: calc(16px + 6 * (100vw / 680));
    width: 50vw;
    padding-left: 5vw;
    padding-top: 20vh;
    text-align: center;
  }
}

.aboutme-section {
  background-color: var(--background-color-light);
  display: flex;
  flex-direction: row;
  padding-top: 3vh;
  padding-bottom: 3vh;
  width: 100vw;
  h1 {
    font-size: calc(3vw + 2px);
  }
  .aboutme-text {
    text-align: left;
    padding-right: 1vw;
    padding-top: 4%;
    font-size: calc(1vw + 6px);
  }

  .aboutme-portrait {
    object-fit: contain;
    width: 100%;
    max-width: 40vw;
    padding-right: 4vw;
    padding-left: 2vw;
  }
}

.skillset-section {
  background: var(--background-color-dark);
  width: 100vw;
  .skillset-text {
    color: var(--background-color-light);
    text-align: center;
    padding-top: 2%;
    font-size: calc(1vw + 10px);
    .credentials {
      font-weight: bold;
      font-size: 2vw;
    }
  }

  .skillset-icons-div-1 {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
  }

  .skillset-icons-div-2 {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
  }

  .skillset-icon-box {
    height: 6vw;
    width: 6vw;
    margin: 1.6vw;
    background: #727272;
    border: 1px solid #5f5f5f;
    box-shadow: 0px 1px 21px 9px rgba(0, 0, 0, 0.19);
    border-radius: 20px;
    .skillset-icon-text {
      color: var(--background-color-light);
      font-size: calc(0.75vw + 5px);
    }
  }

  .skillset-icon {
    object-fit: contain;
    width: 70%;
    height: 70%;
    padding-top: 1vh;
  }
}

.project-section {
  background-color: var(--background-color-light);
  padding-top: 3vh;
  padding-bottom: 3vh;
  text-align: center;
  .carousel-caption {
    padding-top: -30%;
    color: '#5b5b5b';
  }
  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    height: 100px;
    width: 100px;
    padding-top: 40vh;
    background-size: 100%, 100%;
    border-radius: 50%;
    background-image: none;
  }

  .carousel-control-next-icon:after {
    content: '>';
    font-size: 55px;
    color: black;
  }

  .carousel-control-prev-icon:after {
    content: '<';
    font-size: 55px;
    color: black;
  }
}

.contactme-content {
  .contactme-resume {
    overflow: scroll;
  }
  padding-left: 11vw;
  .contactme-icon {
    margin: 0 1vw 3vh 1vw;
    width: 6vw;
  }
  .vl {
    border-left: 1px solid black;
    height: 50vh;
  }
  .contactme-text {
    font-size: calc(1vw + 8px);
    text-align: center;
  }
  .column {
    float: left;
    // padding-left: 3vw;
    padding-right: 3vw;
  }

  .contactme-section:after {
    content: '';
    display: table;
    clear: both;
  }

  @media screen and (max-width: 1000px) {
    .column {
      width: 100%;
    }
  }
  @media screen and (max-width: 1492px) {
    .vl {
      display: none;
    }
  }
}
