@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--background-color-light: #efe4d7;--background-color-dark: #5b5b5b}html{scroll-behavior:smooth}body{background:#efe4d7;background:var(--background-color-light);margin:0;font-family:"Poppins"}nav{background:#5b5b5b;background:var(--background-color-dark);position:fixed;width:100vw;z-index:1}nav .navbar-text{font-weight:bold;color:#efe4d7;color:var(--background-color-light);font-size:1.2rem;padding:10px}nav ul{list-style-type:none;margin:0;padding:0;overflow:hidden;float:right}nav li{float:right;padding-right:1.4vw}nav li a{display:inline-block;color:#efe4d7;color:var(--background-color-light);text-align:center;padding:10px;text-decoration:none;font-size:1.2rem;padding-right:1vw;padding-left:2vw}.main-section{background-color:#efe4d7;background-color:var(--background-color-light);background-image:url(/personal-website/static/media/Frame-1.6ca48d50.png);background-size:cover;background-position:center;width:100vw;height:100vh}.main-section .main-text{font-size:calc(16px + 6 * (100vw / 680));width:50vw;padding-left:5vw;padding-top:20vh;text-align:center}.aboutme-section{background-color:#efe4d7;background-color:var(--background-color-light);display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;padding-top:3vh;padding-bottom:3vh;width:100vw}.aboutme-section h1{font-size:calc(3vw + 2px)}.aboutme-section .aboutme-text{text-align:left;padding-right:1vw;padding-top:4%;font-size:calc(1vw + 6px)}.aboutme-section .aboutme-portrait{object-fit:contain;width:100%;max-width:40vw;padding-right:4vw;padding-left:2vw}.skillset-section{background:#5b5b5b;background:var(--background-color-dark);width:100vw}.skillset-section .skillset-text{color:#efe4d7;color:var(--background-color-light);text-align:center;padding-top:2%;font-size:calc(1vw + 10px)}.skillset-section .skillset-text .credentials{font-weight:bold;font-size:2vw}.skillset-section .skillset-icons-div-1{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;text-align:center;-webkit-justify-content:center;justify-content:center}.skillset-section .skillset-icons-div-2{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;text-align:center;-webkit-justify-content:center;justify-content:center}.skillset-section .skillset-icon-box{height:6vw;width:6vw;margin:1.6vw;background:#727272;border:1px solid #5f5f5f;box-shadow:0px 1px 21px 9px rgba(0,0,0,.19);border-radius:20px}.skillset-section .skillset-icon-box .skillset-icon-text{color:#efe4d7;color:var(--background-color-light);font-size:calc(0.75vw + 5px)}.skillset-section .skillset-icon{object-fit:contain;width:70%;height:70%;padding-top:1vh}.project-section{background-color:#efe4d7;background-color:var(--background-color-light);padding-top:3vh;padding-bottom:3vh;text-align:center}.project-section .carousel-caption{padding-top:-30%;color:"#5b5b5b"}.project-section .carousel-control-prev-icon,.project-section .carousel-control-next-icon{height:100px;width:100px;padding-top:40vh;background-size:100%,100%;border-radius:50%;background-image:none}.project-section .carousel-control-next-icon:after{content:">";font-size:55px;color:#000}.project-section .carousel-control-prev-icon:after{content:"<";font-size:55px;color:#000}.contactme-content{padding-left:11vw}.contactme-content .contactme-resume{overflow:scroll}.contactme-content .contactme-icon{margin:0 1vw 3vh 1vw;width:6vw}.contactme-content .vl{border-left:1px solid #000;height:50vh}.contactme-content .contactme-text{font-size:calc(1vw + 8px);text-align:center}.contactme-content .column{float:left;padding-right:3vw}.contactme-content .contactme-section:after{content:"";display:table;clear:both}@media screen and (max-width: 1000px){.contactme-content .column{width:100%}}@media screen and (max-width: 1492px){.contactme-content .vl{display:none}}
